@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Dela Gothic One";
  src: local("../fonts/Dela/Dela Gothic One Regular"), local("Dela-Gothic-One-Regular"), url("../fonts/Dela/DelaGothicOne-Regular.woff2") format("woff2"), url("../fonts/Dela/DelaGothicOne-Regular.woff") format("woff"), url("../fonts/Dela/DelaGothicOne-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
div,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  overflow-x: hidden;
}

h2,
p,
li {
  word-break: break-word;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  padding: 0;
}

select {
  appearance: none;
}

select:focus,
input:focus,
button:focus {
  outline: none;
  border: none;
}

div {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.timeline {
  display: none;
}
@media (min-width: 1440px) {
  .timeline {
    position: fixed;
    z-index: 999;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    right: 0;
    top: 30%;
    bottom: 0;
    left: auto;
    margin: auto;
  }
}
.timeline .timeline__quad {
  width: 12px;
  height: 12px;
  background-color: transparent;
  border: 1px solid #000000;
  border-radius: 3px;
}

.header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 2;
}
@media (min-width: 1260px) {
  .header {
    justify-content: flex-start;
  }
}
@media (min-width: 1300px) {
  .header {
    padding-left: 50px;
    padding-right: 50px;
  }
}
.header__logo {
  width: 65px;
  height: 65px;
}
@media (min-width: 768px) {
  .header__logo {
    width: 100px;
    height: 100px;
    margin-right: 15px;
  }
}
@media (min-width: 1260px) {
  .header__logo {
    width: 150px;
    height: 150px;
  }
}

.burger-menu {
  position: absolute;
  z-index: 10;
  right: 20px;
  top: 20px;
}
@media (min-width: 768px) {
  .burger-menu {
    display: none;
  }
}

.toggle-menu {
  position: absolute;
  z-index: 99;
  right: 0;
  top: 0;
  width: 44px;
  height: 44px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.line {
  width: 44px;
  height: 3px;
  border-radius: 3px;
  background-color: #000000;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  position: absolute;
  z-index: 1000;
  top: 7px;
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(-45deg) translate(-4px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  position: absolute;
  z-index: 1000;
  top: 17px;
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(45deg) translate(-4px, -4px);
}

.toggle .toggle-menu {
  background-color: transparent;
}

.nav__name {
  color: #ffffff;
  font-size: 24px;
  text-decoration: none;
  padding-bottom: 15px;
}
.nav__button {
  position: relative;
  border: none;
  padding: 10px;
  background-color: transparent;
  box-shadow: none;
}
.nav__overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(244, 169, 192, 0.7);
  top: 0;
  left: 0;
  right: -100%;
  bottom: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}
.nav__content {
  background-color: #f4a9c0;
  padding: 42px 25px 25px;
  height: 100%;
  width: 260px;
  position: absolute;
  margin: 0 auto;
  z-index: 99;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  right: -260px;
  transition: all 0.5s;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
}
.nav__content--open {
  right: 0;
  transition: all ease 0.5s;
}
.nav__logo {
  width: 210px;
}
.nav__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  padding-top: 30px;
}
.nav__signature {
  width: 110px;
  color: #ffffff;
  font-size: 13px;
}

.overlay-visible {
  visibility: visible;
  opacity: 1;
}

.header-desk {
  display: none;
}
@media (min-width: 768px) {
  .header-desk {
    display: block;
    width: 100%;
  }
}
.header-desk__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.header-desk__name {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-size: 14px;
  font-weight: 500;
  color: #000000;
  position: relative;
}
@media (min-width: 1200px) {
  .header-desk__name {
    font-size: 24px;
  }
}

.header-desk__name--color {
  color: #f4a9c0;
}
@media (min-width: 1200px) {
  .header-desk__name--color {
    font-size: 32px;
    align-self: flex-end;
  }
}

@media (min-width: 1200px) {
  .header-desk__item--mod {
    margin-left: auto;
  }
}

.header-desk__item:not(:last-child) {
  padding-right: 15px;
}

.header-desk__name::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 20%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid #000000;
  box-shadow: 0 -1px 0 0 #000000 inset;
  transition: all 0.3s ease;
}

.header-desk__name:hover::after {
  opacity: 1;
  bottom: -2px;
}

.intro {
  margin-top: -240px;
  padding-top: 197px;
  position: relative;
  padding-bottom: 60px;
  background: url("../img/pics/photo/background-intro.jpg") no-repeat;
  background-size: 120%;
  background-position: center;
}
@media (min-width: 490px) {
  .intro {
    background-size: 130%;
    background-position: 50% -50%;
  }
}
@media (min-width: 558px) {
  .intro {
    background-size: 140%;
    background-position: 50% 0;
  }
}
@media (min-width: 920px) {
  .intro {
    background-size: 100%;
  }
}
@media (min-width: 1440px) {
  .intro {
    background-size: 100%;
    background-position: 50% 27%;
  }
}
.intro__heading {
  font-family: "Dela Gothic One", "Arial", sans-serif;
  line-height: 1.1;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 30px;
  position: relative;
  top: 155px;
}
@media (min-width: 768px) {
  .intro__heading {
    font-size: 20px;
  }
}
@media (min-width: 1260px) {
  .intro__heading {
    font-size: 28px;
  }
}
@media (min-width: 1440px) {
  .intro__heading {
    font-size: 36px;
  }
}
.intro__background {
  width: 100%;
  position: absolute;
  top: 0;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
}

.swiper-container {
  overflow: hidden;
  padding-bottom: 15px;
  position: relative;
}

.video-swiper {
  height: 30em;
  top: -65px;
}

.swiper-button-prev,
.swiper-button-next {
  display: inline-flex;
  width: 13px;
  height: 13px;
  z-index: 1;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 25px;
    height: 25px;
  }
}

.swiper-button-prev {
  border-top: 2px solid #000000;
  border-left: 2px solid #000000;
  transform: rotate(-45deg);
}
@media (min-width: 640px) {
  .swiper-button-prev {
    margin-left: 35px;
  }
}
@media (min-width: 1200px) {
  .swiper-button-prev {
    margin-left: 75px;
  }
}

.swiper-button-next {
  border-top: 2px solid #000000;
  border-right: 2px solid #000000;
  transform: rotate(45deg);
}
@media (min-width: 640px) {
  .swiper-button-next {
    margin-right: 35px;
  }
}
@media (min-width: 1200px) {
  .swiper-button-next {
    margin-right: 75px;
  }
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.ideas {
  padding-bottom: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ideas__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
@media (min-width: 960px) {
  .ideas__wrapper {
    flex-direction: row;
    max-width: 1200px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1200px) {
  .ideas__wrapper {
    align-items: flex-start;
  }
}
.ideas__title {
  font-family: "Dela Gothic One", "Arial", sans-serif;
  line-height: 1.1;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 30px;
  font-size: 24px;
  max-width: 590px;
}
@media (min-width: 960px) {
  .ideas__title {
    font-size: 36px;
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .ideas__title {
    font-size: 50px;
    margin-top: 80px;
  }
}

.ideas__wrapper::after {
  position: absolute;
  z-index: 99;
  content: "";
  background: url("../img/pics/elements/el1.png") no-repeat;
  width: 50px;
  height: 128px;
  top: 56px;
  left: 20px;
  rotate: 343deg;
}
@media (min-width: 960px) {
  .ideas__wrapper::after {
    background: url("../img/pics/elements/el1-big.png");
    width: 100px;
    height: 235px;
    top: 275px;
    left: 340px;
  }
}
@media (min-width: 1200px) {
  .ideas__wrapper::after {
    left: 60px;
  }
}

.ideas-slide {
  width: 100%;
}

.ideas__swiper {
  width: 100%;
  max-width: 320px;
  position: relative;
}

.swiper-button-back,
.swiper-button-forward {
  display: inline-flex;
  width: 13px;
  height: 13px;
  z-index: 9;
  cursor: pointer;
  position: absolute;
  top: 50%;
}

.swiper-button-back {
  border-top: 2px solid #ffffff;
  border-left: 2px solid #ffffff;
  transform: rotate(-45deg);
  left: 7px;
}

.swiper-button-forward {
  border-top: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  transform: rotate(45deg);
  right: 7px;
}

.swiper-button-back::after,
.swiper-button-forward::after {
  display: none;
}

.benefits__heading {
  font-family: "Dela Gothic One", "Arial", sans-serif;
  line-height: 1.1;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 30px;
  font-size: 24px;
}
.benefits__nastya {
  width: 100%;
  max-width: 500px;
  margin-bottom: 20px;
}
.benefits__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}
.benefits__item {
  font-family: "Montserrat", "Arial", sans-serif;
}

.benefits__item:not(:last-child) {
  padding-bottom: 20px;
}

html {
  scroll-behavior: smooth;
}